@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,700&display=swap);
html {
  background: #4a90e2;
}

body {
  position: relative;
  background: #f1f4f7;
  left: 5vw;
  top: 5vw;
  height: calc(100vh - 10vw);
  width: 90vw;
  border-radius: 25px;
  border: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-sidebar {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 250px;
  background: #fff;
  border-top-left-radius: 25px;
  border-bottom-left-radius: 25px;
  box-shadow: 2px 2px 3px rgba(255, 255, 255, 0.25);
  overflow: hidden;
}

.user-container {
  padding: 25px;
  text-align: center;
  border-top-left-radius: 25px;
  border-bottom-left-radius: 25px;
  overflow: hidden;
}

.user-icon {
  font-size: 40px;
  color: #4a90e2;
}

.user-name {
  font-size: 20px;
  text-transform: uppercase;
  font-weight: 700;
  color: #4a90e2;
}

.user-role {
  font-weight: 300;
  font-size: 14px;
}

.menu {
  margin: 0;
  padding: 25px;
  list-style: none;
}

.menu-link {
  display: block;
  font-size: 18px;
  padding: 15px;
  color: #8c9ba7;
  position: relative;
}

.menu-link-active {
  color: #4a90e2;
}

.menu-link:hover {
  color: #4a90e2;
}

.link-icon {
  margin-right: 10px;
}
.link-titel {
  display: inline-block;
}

.logout-btn-container {
  position: absolute;
  bottom: 1.5vh;
  left: 1.5vh;
  right: 1.5vh;
}

.logout_btn {
  width: 100%;
  padding: 10px;
  border-radius: 3px;
  border-bottom-left-radius: 20px;
  background: #4a90e2;
  color: #fff;
  border: none;
  outline: none;
  cursor: pointer;
}

.logout_btn:hover {
  background: #41bbff;
}

.students-component {
  z-index: 500;
  position: absolute;
  top: 100px;
  right: 0;
  left: 250px;
  bottom: 0;
  margin: 0;
  border: none;
  border-bottom-right-radius: 25px;
  border-top-right-radius: 25px;
}

.students-component-title {
  z-index: 500;
  text-align: center;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  margin: 0;
}

.dashboard-component {
  padding: 5vh 5% 5% 5%;
  position: absolute;
  top: 0;
  right: 0;
  left: 250px;
  bottom: 10%;
  margin: 0;
  border: none;
  border-top-right-radius: 25px;
  overflow: hidden;
}

.dashboard-title-container {
  display: flex;
  left: 0;
  right: 0;
  position: absolute;
  height: 5vh;
  top: 7%;
  align-items: center;
}

.dashboard-title {
  color: #4a90e2;
  text-align: center;
  width: 100%;
  height: 100%;
}

.main-chart-container {
  position: absolute;
  top: 10%;
  right: 0;
  left: 0;
  bottom: 0;
  margin: 1px;
  overflow: hidden;
}

.main-chart-nav-container {
  margin: 0;
  padding: 0;
  position: absolute;
  bottom: 1.5vh;
  left: calc(250px + 1.5vh);
  right: 1.5vh;
  display: flex;
  justify-content: space-between;
  background: #f1f4f7;
  overflow: hidden;
}

.main-chart-nav-buttons {
  width: 15%;
  text-align: center;
  padding: 10px;
  border-radius: 3px;
  background: #4a90e2;
  color: #fff;
  border: none;
  cursor: pointer;
}

.main-chart-nav-buttons-active {
  color: #00e396;
}

.main-chart-nav-button-6 {
  border-bottom-right-radius: 20px;
}

.main-chart-nav-buttons:hover {
  background: #41bbff;
  border: none;
}

.student-select-buttons-container {
  z-index: 100;
  margin: 0;
  position: absolute;
  top: 1.5vh;
  left: calc(250px + 1.5vh);
  right: 1.5vh;
  display: flex;
  justify-content: center;
  background: #f1f4f7;
}

.student-select-buttons {
  width: 5%;
  text-align: center;
  padding: 10px;
  margin: 5px;
  border-radius: 3px;
  background: #4a90e2;
  color: #fff;
  border: none;
  cursor: pointer;
}

.student-select-buttons:hover {
  background: #00e396;
  border: none;
  outline: none;
}

.student-select-buttons:focus {
  background: #00e396;
  outline: none;
}

